import React from "react";
import { NavLink } from "react-router-dom";
import Logo from '../images/logo.svg'
import setaMenu from '../images/setamenu.svg'
import MenuMobileInativo from '../images/mobilenavinativo.png'
import MenuMobileAtivo from '../images/mobilenavativo.png'

    class Navbar extends React.Component {

        constructor() {

            super()

            this.state = {
                proponenteInfo: false,
                empresaInfo: false,
            }

        }

        exibeProponenteInfo = () => {

            this.setState({
                proponenteInfo: true
            })

        }

        escondeProponenteInfo = () => {

            this.setState({
                proponenteInfo: false
            })

        }

        exibeEmpresaInfo = () => {

            this.setState({
                empresaInfo: true
            })

        }

        escondeEmpresaInfo = () => {

            this.setState({
                empresaInfo: false
            })

        }

        render() {

            return (

                <div className="navbarContainer">

                    <div className="navbarContent">

                        <div className="navbarBranding">

                            <NavLink className="iconeNavbarLogo" to="/" onClick={() => this.props.fechaMenuMobile()}>

                                <img alt="logoSimbio" src={Logo} />

                            </NavLink>

                        </div>

                        <div className="navbarActions">

                            <NavLink className="iconeNavbar" to="/empresas" style={{color: this.state.empresaInfo ? '#1F99A7' : ''}} onMouseEnter={() => this.exibeEmpresaInfo()} onMouseLeave={() => this.escondeEmpresaInfo()}>

                                Para empresas <img alt="setaMenu" src={setaMenu} style={{transform: this.state.empresaInfo ? 'rotate(180deg)' : '', transition: 'ease 0.4s'}} />

                            </NavLink>

                            <NavLink className="iconeNavbar" to="/proponentes" style={{color: this.state.proponenteInfo ? '#1F99A7' : ''}} onMouseEnter={() => this.exibeProponenteInfo()} onMouseLeave={() => this.escondeProponenteInfo()}>

                                Para proponentes <img alt="setaMenu" src={setaMenu} style={{transform: this.state.proponenteInfo ? 'rotate(180deg)' : '', transition: 'ease 0.4s'}} />

                            </NavLink>

                            <NavLink className="iconeNavbar" to="/sobre-nos">

                                Sobre n√≥s

                            </NavLink>

                            |

                            <a className="iconeNavbarAcao" href="https://simbiose.tech">

                                Login

                            </a>

                            <NavLink className="iconeNavbarAcao-servico" to="/cadastro">

                                Contratar servi√ßo

                            </NavLink>

                        </div>

                        {

                            this.state.proponenteInfo && 
                            
                                <div className="proponenteRadar" onMouseEnter={() => this.exibeProponenteInfo()} onMouseLeave={() => this.escondeProponenteInfo()}>Radar de patrocinadores</div>

                        }

                        <div className="imagemMenuMobile" onClick={() => this.props.ativaMenuMobile()}>

                            {

                                this.props.menuMobileAtivo ? 

                                    <img alt="hamburguerMobile" className="imagemMenuMobileTag" src={MenuMobileAtivo} />   

                                :

                                    <img alt="hamburguerMobile" className="imagemMenuMobileTag" src={MenuMobileInativo} />                                

                            }

                        </div>

                    </div>

                </div>
            )
    
        }
    }

export default Navbar;