import React from 'react';
import './Landing.css';
import './components/styling/Proponentes.css';
import './components/styling/Cadastro.css';
import { BrowserRouter , Route , Switch } from 'react-router-dom';
import PageNotFound from "./components/PageNotFound"
import Landing from "./components/Landing"
import Navbar from "./components/Navbar"
import Proponentes from "./components/pages/Proponentes"
import Empresas from "./components/pages/Empresas"
import SobreNos from "./components/pages/SobreNos"
import MenuMobile from './components/actioncomponents/MenuMobile'
import Cadastro from "./components/pages/Cadastro"
import Sair from './images/sair.svg'

class App extends React.Component {

  constructor() {

    super()

    this.state = {
      menuMobileAtivo: false,
      ativaPopUpVideo: false,
    }

  }

  ativaMenuMobile = () => {

    this.setState({
      menuMobileAtivo: !this.state.menuMobileAtivo
    })

  }

  fechaMenuMobile = () => {

    this.setState({
      menuMobileAtivo: false
    })

  }

  ativaPopUpVideo = () => {

    this.setState({
      ativaPopUpVideo: !this.state.ativaPopUpVideo
    })

  }

  fechaPopUpVideo = () => {

    this.setState({
      ativaPopUpVideo: false
    })

  }

  render() {

    return (

      <div className="App">
        {

          this.state.ativaPopUpVideo &&

            <div className="popUpVideoApresentacao">

              <div className="fechaPopUpVideo" onClick={this.fechaPopUpVideo}>

                <img alt="sair" src={Sair} />

              </div>

              <iframe title="apresentacaoSimbiose" className="iframeVideoApresentacao" src="https://www.youtube.com/embed/evHHJAtbEMI" />

            </div>

        }
        <div className="containerLanding" style={{
          filter: this.state.ativaPopUpVideo ? 'blur(4px) grayscale(1)' : '', 
          pointerEvents: this.state.ativaPopUpVideo ? 'none' : 'auto',
          overflowY: this.state.ativaPopUpVideo ? 'hidden' : ''
        }}>      
            <BrowserRouter basename={process.env.PUBLIC_URL}>
              <Navbar 
                ativaMenuMobile={this.ativaMenuMobile}
                menuMobileAtivo={this.state.menuMobileAtivo}
                fechaMenuMobile={this.fechaMenuMobile}
              />
                  <MenuMobile
                    ativaMenuMobile={this.state.menuMobileAtivo}
                    ativaMenuMobileFuncao={this.ativaMenuMobile}
                  />
              <Switch>
                <Route path="/" component={() => <Landing ativaPopUpVideo={this.ativaPopUpVideo} />} exact/>
                <Route path="/proponentes" component={Proponentes} />
                <Route path="/empresas" component={Empresas} />
                <Route path="/sobre-nos" component={SobreNos} />
                <Route path="/Cadastro" component={Cadastro} />
                <Route component={PageNotFound} />
              </Switch>
            </BrowserRouter>
          </div>
      </div>

    )

  }
}

export default App;
